.wrap {
  overflow-x: hidden;
}

.logo {
  height: 40vmin;
  pointer-events: none;
  z-index: 1;
}

@media (prefers-reduced-motion: no-preference) {
  .logo {
    filter: url(#grayscale); /* 흑백 필터 적용 */
    animation: filter-color 2s ease-in-out 1; /* 애니메이션 설정 */
  }
}

@keyframes filter-color {
  from {
    transform: translateX();
  }
}

.link {
  color: #61dafb;
}
